@tailwind base;
body {
  @apply font-sans;
}

a, button {
  @apply transition duration-150;
}

.prose a, .prose-lg a {
  @apply text-c-red !important;
}

.service-type {
  @apply text-white border-2 border-white uppercase py-2 font-bold hover:bg-c-red hover:border-c-red focus:outline-none flex items-center justify-center lg:text-xl;
}
.service-type.active {
  @apply bg-c-red border-c-red;
}
.service-type .service-svg {
  @apply h-6 w-6 mr-2;
}

.annual-service .service-type {
  @apply border-black text-black hover:border-c-red hover:text-white;
}
.annual-service .service-type.active {
  @apply border-c-red text-white;
}
.annual-service .service-content-container p {
  @apply text-center;
}
.annual-service .service-content-container .prose {
  @apply text-black;
}
.annual-service .service-content-container .prose ul > li:before {
  @apply bg-c-red;
}
.annual-service .service-content-container .service-item {
  @apply hidden;
}
.annual-service .service-content-container .service-item.active {
  @apply block;
}

.gfield {
  @apply mb-4;
}
.gfield input, .gfield select, .gfield textarea {
  @apply w-full focus:outline-none border border-[#707070];
}
.gfield.gfield_error input, .gfield.gfield_error select, .gfield.gfield_error textarea {
  @apply border-2 border-red-500;
}
.gfield.gform_validation_container {
  @apply hidden;
}
.gfield .gfield_required {
  @apply hidden;
}
.gfield textarea {
  @apply h-40;
}
.gfield label {
  @apply block mb-1;
}

.gform_submission_error {
  @apply text-red-500 text-center mb-2 font-bold;
}

.hidden_label label {
  @apply hidden;
}

.gfield_visibility_hidden {
  @apply hidden !important;
}

.validation_message {
  @apply hidden;
}

.validation_error {
  @apply text-red-500 px-0 mb-4 md:mb-4 md:w-4/5 md:mx-auto md:text-center;
}

.gform_footer .gform_button {
  @apply text-white bg-c-red uppercase font-bold py-3.5 w-full hover:cursor-pointer;
}

.gform_drop_area {
  @apply bg-[#F8F8F8] border-dashed border-2 border-[#9D9D9D] flex justify-center py-8 relative;
}
.gform_drop_area button {
  @apply text-c-red underline font-semibold ml-1;
}
.gform_drop_area:before {
  @apply h-10 w-10 absolute left-0 md:ml-2;
  content: " ";
  background-image: url("../images/attach.svg");
  top: calc(50% - 1.25rem);
}

.gform_fileupload_rules {
  @apply hidden;
}

.gform_delete_file {
  @apply text-c-red;
}
.gform_delete_file .screen-reader-text {
  @apply hidden;
}

.ginput_preview {
  @apply flex items-center pt-1;
}

.home-form .gfield {
  @apply sm:mb-2 lg:mb-3;
}
.home-form .gform_body {
  @apply sm:mb-3;
}
.home-form .gform_fields {
  @apply sm:grid sm:grid-cols-6 sm:gap-x-4 lg:gap-x-6;
}
.home-form .gform_fields .gfield.medium {
  @apply sm:col-span-3;
}
.home-form .gform_fields .gfield.small {
  @apply sm:col-span-2;
}

.repair-form #field_3_2 .gfield_validation_message, .repair-form #field_4_2 .gfield_validation_message {
  @apply block text-red-500 text-sm w-full;
}

.service-form .gfield {
  @apply sm:flex sm:items-center mb-6 sm:mb-12 sm:flex-wrap;
}
.service-form .gfield label {
  @apply sm:mr-4 lg:mr-6;
}
.service-form .gfield .ginput_container {
  @apply flex-grow;
}
.service-form .gfield .gfield_validation_message {
  @apply block text-red-500 text-sm ml-2 w-full text-center;
}

@tailwind components;
@tailwind utilities;